
import { css } from 'lit';
export default css`
/* Apply standardized box sizing to the component. */
:host {
  box-sizing: border-box;
}
:host *,
:host *::before,
:host *::after {
  box-sizing: inherit;
}
/* Apply proper CSS for accessibly hiding elements to each component. */
.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px;
  height: 1px;
  word-wrap: normal;
}
/* Apply component specific CSS */
.pulse{max-height:100%;max-width:var(--max-size);position:absolute}.top-right{right:0;top:0;transform:rotate(0deg)}.bottom-right{bottom:0;right:0;transform:rotate(90deg)}.bottom-left{bottom:0;left:0;transform:rotate(180deg)}.pulse-top-aligned{bottom:none;top:-20px}.top-left{left:0;top:0;transform:rotate(270deg)}@media (prefers-reduced-motion:no-preference){.pulse{--scale-speed:0.15}.arc-1,.arc-2,.arc-3,.arc-4{transform-origin:top right}.pulse:hover .arc-1{--scale:1.5;animation:calc(var(--scale-speed)*1s) ease-in 0s 2 alternate animating-multiple-properties}.pulse:hover .arc-2{--scale:1.3;animation:calc(var(--scale-speed)*1s) ease-in .1s 2 alternate animating-multiple-properties}.pulse:hover .arc-3{--scale:1.1;animation:calc(var(--scale-speed)*1s) ease-in .2s 2 alternate animating-multiple-properties}.pulse:hover .arc-4{--scale:1.05;animation:calc(var(--scale-speed)*1s) ease-in .3s 2 alternate animating-multiple-properties}@keyframes animating-multiple-properties{0%{transform:scale(1)}to{transform:scale(var(--scale))}}}`;