  requestsBufferMap = new Map<string, boolean>();

  checkBrowser() {
    const ua = navigator.userAgent;
    let tem,
      M =
        ua.match(
          /(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i
        ) || [];
    if (/trident/i.test(M[1])) {
      tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
      return { name: 'IE', version: tem[1] || '' };
    }
    if (M[1] === 'Chrome') {
      tem = ua.match(/\bOPR|Edge\/(\d+)/);
      if (tem != null) {
        return { name: 'Opera', version: tem[1] };
      }
    }
    M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
    if ((tem = ua.match(/version\/(\d+\.\d+)/i)) != null) {
      M.splice(1, 1, tem[1]);
    }
    return {
      name: M[0],
      version: parseFloat(M[1]),
    };
  }

  /*
   * Set body padding as CSS variable to account for sticky header (padding itself gets applied in shared.css)
   */
  setBodyPadding() {
    const stickyHeaderHeight =
      this.shadowRoot?.querySelector('.sticky-header')?.clientHeight;

    if (stickyHeaderHeight) {
      document
        .querySelector('body')!
        .style.setProperty('--sticky-header-height', stickyHeaderHeight + 'px');
    }
  }

  /*
   * Create a Map for any menu that request mobile mode
   */
  private processRequests(menuId: string, mode: string) {
    if (mode === 'mobile') {
      this.requestsBufferMap.set(menuId, true);
    } else if (this.requestsBufferMap.has(menuId)) {
      this.requestsBufferMap.delete(menuId);
    }

    // set header mobile mode if at least 1 menu requires mobile mode
    this.isMobile = this.requestsBufferMap.size > 0;
    this.setBodyPadding();
  }

  private _menuModeListener(e: CustomEvent) {
    this.processRequests(e.detail.menuId, e.detail.mode);
  }

  _trapKeyboardMobileMenu(event: KeyboardEvent) {
    if (event.key === 'Tab' && !event.shiftKey) {
      // After opening mobile menu, next tab will focus on first menu item. This also traps the keyboard to go through menu items until hamburger menu is closed.

      // Only trap the keyboard if the active element is the menu dropdown
      if (
        !document.activeElement?.shadowRoot?.activeElement?.matches(
          'button.menu-dropdown'
        )
      ) {
        return;
      }

      // Get all focusable elements in the menu
      const focusableElements =
        this.shadowRoot!.querySelector(
          'tufts-menu'
        )!.shadowRoot!.querySelectorAll('a[href], button');

      if (!focusableElements) {
        return;
      }
      event.preventDefault();

      const firstFocusableElement = focusableElements[0] as HTMLElement;
      firstFocusableElement.focus();
    }
  }

  _trapKeyboardMobileSearch(event: KeyboardEvent) {
    if (event.key !== 'Tab') {
      return;
    }

    // Get the search button
    const searchButton = this.shadowRoot!.querySelector(
      '.open-search-button'
    ) as HTMLElement;

    // Get all focusable elements in the menu
    const focusableElements = this.shadowRoot
      ?.querySelector('#search-wrapper')
      ?.querySelectorAll('a[href], button, input');

    // If focusable elements are returned, then get the first and last elements
    if (focusableElements) {
      const firstSearchPanelElement = focusableElements[0] as HTMLElement;
      const lastSearchPanelElement = focusableElements[
        focusableElements.length - 1
      ] as HTMLElement;

      // If the active element is the search button, then the next tab will go to the first element in the search panel. If the active element is the last element in the panel, the next tab will go to the search button.
      if (document.activeElement?.shadowRoot?.activeElement === searchButton) {
        event.preventDefault();
        firstSearchPanelElement.focus();
      } else if (
        document.activeElement?.shadowRoot?.activeElement ===
        lastSearchPanelElement
      ) {
        event.preventDefault();
        searchButton!.focus();
      }
    }
  }

  render() {
    return html`
      ${this.browserInfo?.name === 'Safari' && this.browserInfo?.version < 15.5
        ? html`<div class="outdated-browser-banner">
            <tufts-icon
              library="material"
              name="error"
              size="1.5rem"
            ></tufts-icon>
            ${this.warningText}
          </div>`
        : null}

      <header
        @menuMode=${this._menuModeListener}
        class=${classMap({
          mobile: this.isMobile,
          // 'is-sticky': this.isSticky,
        })}
      >
        <div class="tufts-main-header">
          ${this.isMobile ? this.mobileTemplate() : this.desktopTemplate()}
        </div>
        ${this.isMobile ? this.stickyMobileTemplate() : ''}
      </header>
    `;
  }

  toggleHamburgerMenu() {
    // USE THIS IF WE WANT TO GO BACK TO ANIMATE MENU (WIP FOR LATER)
    // this.hamburgerMenuOpen
    //   ? this.collapseSection(this.mobileMenu)
    //   : this.expandSection(this.mobileMenu);
    this.hamburgerMenuOpen = !this.hamburgerMenuOpen;

    if (this.hamburgerMenuOpen) {
      this.searchDropdownStatus = false;
      document.querySelector('body')!.style.overflow = 'hidden';
      this._trapKeyboardMobileMenu = this._trapKeyboardMobileMenu.bind(this);
      document.addEventListener('keydown', this._trapKeyboardMobileMenu);
    } else {
      document.querySelector('body')!.style.overflow = 'revert';
      document.removeEventListener('keydown', this._trapKeyboardMobileMenu);
    }
  }

