import { CSSResultGroup, TemplateResult, html } from 'lit';
import { OutlineElement } from '../../base/outline-element/outline-element';
import { customElement, property } from 'lit/decorators.js';
import { ifDefined } from 'lit/directives/if-defined.js';
import { classMap } from 'lit/directives/class-map.js';
import componentStyles from './tufts-container.css.lit';
import '../tufts-pulse/tufts-pulse';
import { ResizeController } from '../../controllers/resize-controller';
import { RevealTrigger } from '../../controllers/reveal-trigger';

type containerWidths = 'wide' | 'medium' | 'narrow' | 'full';

type backgroundColor =
  | 'white'
  | 'blue'
  | 'blue_dark'
  | 'purple'
  | 'purple_dark'
  | 'green'
  | 'tint'
  | 'grey'
  | 'transparent';

/**
 * The TuftseContainer component
 * @element tufts-container
 *
 * @slot - default
 * @attr top-margin
 * @attr bottom-margin
 * @attr padding
 * @attr pulse-orientation
 * @attr justify-end
 * @attr container-width
 * @attr background-color
 * @attr max-pulse-size
 * @attr pulse-mobile
 */
@customElement('tufts-container')
export class TuftsContainer extends OutlineElement {
  static styles: CSSResultGroup = [componentStyles];
  resizeController: ResizeController;
  revealTrigger: RevealTrigger;

  // The resizeController is initialized here because displayPulseWidth is not set yet when constructor runs
  connectedCallback() {
    super.connectedCallback();
    if (this.pulseOrientation) {
      this.resizeController = new ResizeController(this, {
        breakpoints: [this.displayPulseWidth],
      });
    }
  }

  /**
   * The vertical space from the component above it (using Utopia fluid space variables defined in outline.theme.css, ie. space-3xl)
   **/
  @property({
    type: String,
    reflect: true,
    attribute: 'top-margin',
  })
  topMargin: string;

  @property({
    type: String,
    reflect: true,
    attribute: 'bottom-margin',
  })
  bottomMargin: string;

  @property({
    type: String,
    reflect: true,
    attribute: 'padding',
  })
  padding = 'space-l';

  @property({
    type: String,
    reflect: true,
    attribute: 'pulse-orientation',
  })
  pulseOrientation: null;

  @property({
    type: String,
    reflect: true,
    attribute: 'pulse-top-alignment',
  })
  pulseTopAlignment: null;

  @property({
    type: Boolean,
    reflect: true,
    attribute: 'justify-end',
  })
  justifyEnd = false;

  @property({
    type: String,
    reflect: true,
    attribute: 'container-width',
  })
  containerWidth: containerWidths = 'wide';

  @property({
    type: String,
    reflect: true,
    attribute: 'background-color',
  })
  backgroundColor: backgroundColor = 'white';

  @property({
    type: String,
    reflect: true,
    attribute: 'max-pulse-size',
  })
  maxPulseSize = '650px';

  @property({
    type: String,
    reflect: true,
    attribute: 'gap',
  })
  gap = '0px';

  /**
   * Width at which the pulse is removed
   */
  @property({
    type: String,
    reflect: true,
    converter: Number,
    attribute: 'display-pulse-width',
  })
  displayPulseWidth = 1080;

  @property({ type: String, reflect: true, attribute: 'pulse-mobile' })
  pulseMobile = null;

  @property({ type: String, reflect: true, attribute: 'animation-type' })
  animationType: string;

  firstUpdated() {
    if (this.animationType) {
      this.revealTrigger = new RevealTrigger(this, {
        triggerSelector: '.animation',
        revealClass: this.animationType,
      });
    }
  }

  /**
   * By default, pulse is displayed only on desktop screens when pulse-orientation is set.
   * In the case where 'pulse-mobile' is true, the pulse is displayed at all resolutions.
   */
  displayPulse() {
    if (!this.pulseOrientation) {
      return false;
    }
    // if the resize controller is not defined yet, return false
    if (this.resizeController.currentBreakpointRange === undefined) {
      return false;
    }
    if (
      this.pulseMobile === 'true' ||
      this.resizeController.currentBreakpointRange !== 0
    ) {
      return true;
    }
    return false;
  }

  pulseTemplate(): TemplateResult {
    return html`
      ${this.displayPulse()
        ? html`<tufts-pulse
            orientation="${ifDefined(this.pulseOrientation)}"
            max-size="${ifDefined(this.maxPulseSize)}"
            pulse-top-alignment="${ifDefined(this.pulseTopAlignment)}"
          ></tufts-pulse>`
        : null}
    `;
  }
  render(): TemplateResult {
    const containerWidth = this.containerWidth;
    let minPulseHeight = '0';
    // Pulse should always have a min-height
    if (
      this.pulseOrientation &&
      this.resizeController.currentBreakpointRange !== 0
    ) {
      minPulseHeight = `min(25rem, ${this.maxPulseSize})`;
    }
    const classes = {
      container: true,
      [containerWidth]: true,
      justifyEnd: this.justifyEnd,
      [this.backgroundColor]: this.backgroundColor,
    };
    const containerClass = {
      'container-wrapper': true,
      'animation': !!this.animationType,
    };
    return html`
      ${this.topMargin || this.bottomMargin || this.backgroundColor
        ? html` <style>
            .container-wrapper {
              background-color: var(--bg-color--${this.backgroundColor});
              padding-block: var(--${this.padding});
              min-height: ${minPulseHeight};
            }
            .container {
              margin-top: var(--${this.topMargin});
              margin-bottom: var(--${this.bottomMargin});
              --tufts-container-space-between-elements: ${this.gap};
            }
          </style>`
        : ''}
      <div class="${classMap(containerClass)}">
        ${this.pulseTemplate()}
        <div class="${classMap(classes)}">
          <slot></slot>
        </div>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'tufts-container': TuftsContainer;
  }
}
